import TabBar from '../../components/tabbar/tabbar.vue'
import {ajax} from '../../common/js/util.js'
import toast from '../../common/js/toast.js'
export default {
	data() {
		return {
			itemIndex: 2,
			Color: '#fff',
			backColor: '#3175E4',
			user: '',
			head: '',
			list: {},
			about: ''
		}
	},
	components: {
		TabBar
	},
	onShow() {
		this.init()
	},
	methods: {
		init() {
			let self = this
			let datas = {
				uid: uni.getStorageSync('uid')
			}
			toast.showLoading('加载中...')
			let data = {
				url: '/user/center',
				data: datas,
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: function(res) {
					console.log(res)
					if (res.data.success == true) {
						toast.hideLoading()
						self.list = res.data.body
						self.user = res.data.body.nickName
						self.head = res.data.body.avatarUrl
						self.about = res.data.body.aboutUsUrl
						self.$store.commit('changeHead', res.data.body.avatarUrl)
					}
				}
			}
			ajax(data)
		},
		changeItem(k) {
			if (k == 1) {
				uni.switchTab({
					url: '/pages/index/index'
				})
			} else if (k == 2) {
				uni.switchTab({
					url: '/pages/person/person'
				})
			} else if (k == 3) {
				uni.navigateTo({
					url: '../law/law'
				})
			}
		},
		goSui() {
			uni.navigateTo({
				url: '../suishoupai/suishoupai'
			})
		},
		goLv() {
			uni.navigateTo({
				url: '../falvshensu/falvshensu'
			})
		},
		goXing() {
			uni.navigateTo({
				url: '../xingshishensu/xingshishensu'
			})
		},
		goMin() {
			uni.navigateTo({
				url: '../minshishensu/minshishensu'
			})
		},
		goXingZheng() {
			uni.navigateTo({
				url: '../xingzhengshensu/xingzhengshensu'
			})
		},
		goJu() {
			uni.navigateTo({
				url: '../jubaojilu/jubaojilu'
			})
		},
		goAbout() {
			uni.navigateTo({
				url: '../about/about?url='+this.about
			})
		}
	}
}
